@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

html,
body,
#root,
.App {
  background-image: url(/static/media/Background.b8e0d132.jpg);
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  text-align: center;
}

.modal-wrapper {
  position: fixed !important;
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
}

.modal-backdrop {
  position: fixed !important;
  top: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  right: 0 !important;
  z-index: 100 !important;
  background-color: rgba(0, 0, 0, 0.3) !important;
}

.modal-box {
  position: relative !important;
  float: right !important;
  height: 100% !important;
  width: 20% !important;
  overflow-y: auto !important;
  background-color: white !important;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25) !important;
  z-index: 101 !important;
  text-align: center !important;
  background-image: url(/static/media/NavBarBackground.ad5aa8cc.jpg) !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}

.div-nav-bar {
  height: 100%;
  width: 100%;
  display: table;
}

.div-nav-elements {
  display: table-cell;
  vertical-align: middle;
}

ul {
  list-style: none;
  margin-bottom: 20px;
}

ul li {
  padding: 20px;
}

ul li a {
  color: white;
  text-decoration: none;
  font-size: xx-large;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

ul li a:hover {
  color: #ffcc00;
}

.icon {
  float: right;
  margin: 20px;
  color: #ffcc00;
}

.icon:hover {
  color: white;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .modal-box {
    width: 40% !important;
  }
}

.home {
  height: 100%;
}

.title {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  cursor: default;
}

.recipes {
  color: #ffcc00;
  font-size: 300px;
  font-weight: 400;
  font-family: "Dancing Script", cursive;
}

@media screen and (max-width: 500px) {
  .recipes {
    font-size: 900%;
  }
}

.tb {
  display: table;
  width: 100%;
}

.td {
  display: table-cell;
  vertical-align: middle;
}

.input-bar,
.button-bar {
  color: white;
  font-family: Nunito;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  outline: none;
}

.container {
  position: absolute;
  top: 5%;
  left: 0;
  right: 0;
  width: 100%;
  padding: 20px;
  background-color: #ffd633;
  border-radius: 20px;
  box-shadow: 0 10px 40px #ffeb99, 0 0 0 20px #ffffff;
  transform: scale(0.6);
}

.input-bar[type="text"] {
  width: 100%;
  height: 100%;
  font-size: 60px;
  line-height: 1;
}

.input-bar[type="text"]::-webkit-input-placeholder {
  color: #fff0b3;
}

.input-bar[type="text"]:-ms-input-placeholder {
  color: #fff0b3;
}

.input-bar[type="text"]::-ms-input-placeholder {
  color: #fff0b3;
}

.input-bar[type="text"]::placeholder {
  color: #fff0b3;
}

.cover {
  width: 1px;
  padding-left: 35px;
}

.button-bar {
  position: relative;
  display: block;
  width: 84px;
  height: 96px;
  cursor: pointer;
}

.button-bar:hover {
  outline: none;
}

.circle {
  position: relative;
  top: -8px;
  left: 0;
  width: 65px;
  height: 65px;
  margin-top: 0;
  border-width: 15px;
  border: 15px solid white;
  background-color: transparent;
  border-radius: 50%;
  transition: 0.5s ease all;
}

.button-span {
  position: absolute;
  top: 68px;
  left: 43px;
  display: block;
  width: 45px;
  height: 15px;
  background-color: transparent;
  border-radius: 10px;
  transform: rotateZ(52deg);
  transition: 0.5s ease all;
}

.button-span:before,
.button-span:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 45px;
  height: 15px;
  background-color: white;
  border-radius: 10px;
  transform: rotateZ(0);
  transition: 0.5s ease all;
}

#cover:hover .circle {
  top: -1px;
  width: 67px;
  height: 15px;
  border-width: 0;
  background-color: white;
  border-radius: 20px;
}

#cover:hover .button-span {
  top: 50%;
  left: 56px;
  width: 25px;
  margin-top: -9px;
  transform: rotateZ(0);
}

#cover:hover .button-span:before {
  bottom: 11px;
  transform: rotateZ(52deg);
}

#cover:hover .button-span:after {
  bottom: -11px;
  transform: rotateZ(-52deg);
}
#cover:hover .button-span:before,
#cover:hover .button-span:after {
  right: -6px;
  width: 40px;
  background-color: white;
}

.deck {
  width: 100%;
  margin: 0 !important;
  margin-bottom: 5% !important;
}

.recipe {
  margin-left: 6% !important;
  margin-right: 6% !important;
}

.recipe:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 1);
}

.recipe-card {
  position: absolute;
  top: 40%;
}

.label {
  color: black;
}

.custom-card {
  right: 5%;
  left: 5%;
  width: 90%;
  position: absolute;
  top: 15%;
}

.card-header {
  background-color: #ffcc00;
}

.card-body {
  background-color: #ffeb99;
}

.custom-file-label {
  z-index: 0 !important;
}

